$(".js-hero-slider").slick({
  arrows: true,
  // autoplay: true,
  fade: true,
  mobileFirst: true,
  cssEase: "linear",
  dots: true,
  prevArrow: false,
  nextArrow:
    '<button type="button" class="hero-slider__btn"></button>',
});

$(".js-vr-slider").slick({
  arrows: false,
  variableWidth: true,
  mobileFirst: true,
  swipeToSlide: true,
});

$(".js-ps-slider").slick({
  arrows: false,
  variableWidth: true,
  mobileFirst: true,
  swipeToSlide: true,
});

$(".js-service-slider").slick({
  arrows: false,
  variableWidth: true,
  mobileFirst: true,
  responsive: [
    {
      breakpoint: 1000,
      settings: "unslick",
    }
  ]
});

$(".js-comments-slider").slick({
  arrows: true,
  variableWidth: true,
  mobileFirst: true,
  swipeToSlide: true,
  prevArrow:
    '<button type="button" class="comments-slider__btn comments-slider__btn--prev"></button>',
  nextArrow:
    '<button type="button" class="comments-slider__btn comments-slider__btn--next"></button>',
});
